import React from 'react';
import './BuyDroplet.scss';
import {Link} from 'react-router';
import {connect} from 'react-redux';

import Sidebar from '../../User/Sidebar/components/Sidebar.js';
import BuyConfirmation from '../../Widgets/BuyConfirmation/components/BuyConfirmation.js';
import InviteFriends from '../../Widgets/InviteFriends/components/InviteFriends.js';
import Header from '../../Header/components/Header.js';

import logoImg from '../assets/droplet-logo.png';
import logOutImg from '../assets/logout-icon.png';

class BuyDropletContainer extends React.Component {
    constructor(props) {
        super(props);
    }

    componentDidMount(){
      if(!this.props.auth.loggedIn){
        this.goToIndex();
      }
    }

    onChange = () => {}

    goToIndex = () => {
        this.props.router.push('/');
    }

    render() {
        return (
            <div className="buy-droplet">
              <Header goHome={this.goToIndex} isLoggedIn={true} />
              <Sidebar />
              <div className="buy-section">
                <div id="title2" className="title-text">Buy Droplet</div>
                <div className='buy-widget'>
                  <BuyConfirmation />
                </div>
              </div>
              <div className="invite-section">
                <div id="title1" className="title-text">Invite Friends</div>
                <div className='invite-widget'>
                  <InviteFriends />
                </div>
              </div>
            </div>
        );
    }
}

const mapStateToProps = (state) => ({ auth: state.auth })

export default connect(mapStateToProps)(BuyDropletContainer);



