import React from 'react';
import './BuyConfirmation.scss';
import {Link} from 'react-router';

class BuyConfirmation extends React.Component {
    constructor(props) {
        super(props);
    }

    render() {
        return (
          <div className='buy-confirmation'>
            <div className="detail-section">
              <h4>We got you covered!</h4>
              <p>You can now see your cover in your Droplet History.</p>
              <p>An email will also be sent to you to confirm your Droplet dates.</p>
              <p>Now go forth and #embracetherain</p>
              <button className="btn btn-green btn-buy"><Link to="/user-buyDroplet">Buy More Droplets?</Link></button>
            </div>
          </div>
        );
    }
}


export default BuyConfirmation;



